<template>
    <link rel="stylesheet" href="//at.alicdn.com/t/font_2003341_772si01v72m.css">
    <div class="evaluates">
        <div class="Satisfaction">
            <span class="all" :class="{alls:alls}" @click="all">全部({{ratings.length}})</span>
            <span class="yes" :class="{yess:yess}"  @click="yeses">满意({{yesum.length}})</span>
            <span class="no" :class="{nos:nos}"  @click="noes">不满意({{nosum.length}})</span>
        </div>
        <div class="comment" @click="toggle" :class="{active:active}">
            <span class="text"><i class="iconfont icon-duihao21" :class="{colors:active}"></i>只看评论内容</span>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'evaluate',
        props:['ratings'],
        data(){
            return{
                alls:true,
                yess:false,
                nos:false,
                active:false
            }
        },
        methods:{
            all(){
                this.alls=true;
                this.yess=false;
                this.nos=false;
                this.$emit("child-event",2);
            },
            yeses(){
                this.yess=true;
                this.nos=false;
                this.alls=false;
              this.$emit("child-event",0);
            },
            noes(){
                this.nos=true;
                this.yess=false;
                this.alls=false;
                this.$emit("child-event",1);
            },
            toggle(){
                this.active=!this.active;
                this.$emit("child-event2",this.active);
            }
        },
        computed: {
            yesum() {
                return this.ratings.filter(function (item) {
                    return item.rateType === 0;
                })
            },
            nosum() {
                return this.ratings.filter(function (item) {
                    return item.rateType === 1;
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    .evaluates{
        height: 85px;
    .Satisfaction{
        color: white;
        width: 100%;
        position: relative;
        margin-left: 14px;
        margin-right: 14px;
        span{

             width: 80px;
             margin-left: 20px;
             border-radius: 1px;
             text-align: center;
         }
        .all{
            display: inline-block;
            background-color: #b3e5fc;
            height: 30px;
            color: black;
            line-height: 30px;
        }
        .alls{
            background-color: #00b0ff;
            color: white;
        }
        .yes{
            display: inline-block;
            margin-top: 10px;
            background-color: #b3e5fc;
            color: black;
            height: 30px;
            line-height: 30px;
        }
        .yess{
            background-color:#00b0ff;
            color: white;
        }
        .no{
            display: inline-block;
            background-color: gainsboro;
            color: black;
            height: 30px;
            line-height: 30px;
        }
        .nos{
            background-color: grey;
            color: white;
        }
    }
    .comment{
        .text{
            margin-left: 36px;
        }
        .colors{
            color: green;
        }
    }
    }
</style>