<template>
<div class="control">
    <transition name="shows">
        <div class="changed" v-if="foodCopy" >
    <i  v-if="foodCopy" v-show="foodCopy.count>0" class="iconfont icon-jianhao" @click.stop.prevent=" decrease"></i>
    <div  v-if="foodCopy" class="count" v-show="foodCopy.count>0">{{foodCopy.count}}</div>
       </div>
    </transition>
    <i class="iconfont icon-jiahao" @click.stop.prevent="adds($event)"></i>
</div>
</template>
<script>
    export default {
        name: 'control',
        props:['food'],
        data(){
            return{

            }
        },
        methods:{
            adds(){
                console.log(123);
                this.foodCopy.count++;
            },
            decrease(){
              if(this.foodCopy.count){
                  this.foodCopy.count--;
              }
            }
        },
        computed:{
            foodCopy(){
                return this.food;
            }
        }
    }
</script>

<style lang="scss">
     .control{
         display: inline-block;
         right: 24px;
         width: 85px;
         position: absolute;
         .iconfont{
             font-size: 20px;
             color: #00b0ff;
         }
         .changed{
             display: inline-block;
             .count{
                 display: inline-block;
                 width: 30px;
             }
         }
         .icon-jiahao{
             right: 24px;
             position: absolute;
         }
         &.shows-enter-active{
             animation: ani3 1s ;
         }
         &.shows-leave-active{
             animation: ani3 1s reverse;
         }
         @keyframes ani3 {
           from{
               opacity: 0;

               transform: translate3d(100%,0,0)rotate(180deg);

           }
             to{
                 opacity: 1;
                 transform: translate3d(0,0,0);
             }
         }
     }
</style>