<template>
    <v-header :header-seller="seller"></v-header>
    <ul class="option">
        <li><a><router-link to="/goods">商品</router-link></a></li>
        <li><a><router-link to="/ratings">评论</router-link></a></li>
        <li><a><router-link to="/seller">商家</router-link></a></li>
    </ul>
    <router-view :goods="goodes" :seller="seller" :rating="ratings" v-if="goodes.length"></router-view>
    <shopping :goods="goodes" :seller="seller"></shopping>
</template>
<script>
    import header from "./components/header/header";
    import shopping from "./components/shopping/shopping"
    export  default {
        name:'#app',
        components:{
          'v-header':header,
            shopping
        },
        data() {
          return {
              goodes:[],
              ratings:[],
              seller: {},
              showdetail:false
          }
        },
        methods:{
            opendetail(){
                this.showdetail=true;
            },
            closedetail(){
                this.showdetail=false;
            }
        },
        props:['http'],
        created(){
            this.http('/seller','post',{username:'lisi'}).then((res)=>{
                var obj=res.data.data;
                this.seller=obj;
                //console.log(this.seller);
            }),
                this.http('/goods','post',{username:'lisi'}).then((res)=>{
                    var obj=res.data.data;
                    this.goodes=obj;
                    this.goodes.forEach((item)=> {
                        //

                        //this.$set(food,'count',1); //vue2.x
                        item.foods.forEach((food)=>{
                        const handler = {
                            get: function (target, key, receiver) {
                                //console.log(`getting ${key}!`);
                                return Reflect.get(target, key, receiver);
                            },
                            set: function (target, key, value, receiver) {
                                //console.log(`setting ${key}!`);
                                return Reflect.set(target, key, value, receiver);
                            }
                        };
                        const obj = new Proxy(food, handler);
                        obj.count = 0; // setting a!
                        })
                    });
                    //console.log(this.goodes);
                }),
                this.http('/ratings','post',{username:'lisi'}).then((res)=>{
                    var obj=res.data.data;
                    this.ratings=obj;
                    //console.log(this.ratings);
                })
            }
    }
</script>
