<template>
    <link rel="stylesheet" href="//at.alicdn.com/t/font_2003341_u15bpo53beh.css">
    <transition name="move">
<div class="food" v-show="select" ref="food">
    <div class="head">
        <div class="img-wrapper">

            <img class="img" :src="food.image"/>
            <div class="foce" @click="closed"><i class="iconfont icon-arrow-left-bold"></i></div>
        </div>
        <div class="name">
            <p>{{food.name}}</p>
            <p class="text"><span>月售{{food.sellCount}}份</span><span>好评率{{food.rating}}</span></p>
        </div>
        <div class="price">
            <p>¥{{food.price}}</p>
            <div class="shop">
                <div class="shopcart" v-if="food.count<1" v-show="food.count<1" @click="asde">加入购物车</div>
                <control :food="food"></control>
            </div>
        </div>
    </div>
    <div class="wrapper" v-show="food.info">
       <p class="title">商品信息</p>
        <p class="info">{{food.info}}</p>
    </div>
    <div class="content">
        <h5>商品评价</h5>
        <evaluate :ratings="food.ratings" @child-event="parentEvent"  @child-event2="parentEvent2" v-if="food.ratings"></evaluate>
    </div>
    <div class="message">
        <ul v-show="food.ratings && food.ratings.length">
            <li class="food-mess" v-show="needShow(item.rateType,item.text)" v-for="(item,index) in food.ratings" :key="index">
                <span class="time">{{format(item.rateTime)}}</span>
                <div class="text">
                    <span class="use">{{item.username}}</span>
                    <img width="17" height="17" :src="item.avatar"/>
                </div>
                <p class="icon">
                    <i class="iconfont icon-dianzan" v-if="item.rateType===0"></i>
                    <i class="iconfont icon-chaping" v-if="item.rateType===1"></i>
                    <span>{{item.text}}</span>
                </p>
            </li>
        </ul>
        <div v-show="!food.ratings || food.ratings.length===0" class="no-rating">暂无评价</div>
    </div>
</div>
    </transition>
</template>
<script>
    import evaluate from "../evaluate/evaluate"
    import {formatDate} from "../js/formatDate"
    import control from "../control/control";
    export default {
        components: {
            "evaluate":evaluate,
            control
        },
        name: 'food',
        props:['food'],
        data(){
            return{
                active:false,
                type:2,
                select:false,
            }
        },
        methods:{
            show(){
              this.select=true;
            },
            closed(){
                this.select=false;
            },
            asde(){
                this.fs.count++;
                console.log(this.fs.count);
            },
            format(time) {
                let date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            },
            parentEvent(type){
                this.type=type;
            },
            parentEvent2(active){
                this.active=active;

            },
            needShow(t,text){
                if(this.active){
                    if (this.type==2 && text!=''){
                        return true;
                    }else if (this.type==t&&text!=''){
                        return true;
                    }
                }else {
                    if (this.type==2){
                        return true
                    }else if (this.type==t){
                        return true;
                    }
                }
            }
        },
        computed:{
           fs(){
               return this.food
           }
        }
    }
</script>

<style lang="scss">
    .food{
        overflow: hidden;
        overflow-y: scroll;
        width: 100%;
        position: fixed;
        top:0px;
        left: 0;
        z-index: 10;
        bottom: 48px;
        background-color: white;
        .head{
            .img-wrapper{
                width: 100%;
                height: 0px;
                padding-top: 100%;
                position: relative;
                .foce{
                    position: absolute;
                    top: 10px;
                    left: 0;
                  i{
                      font-size: 36px;
                      color: white;
                      display: block;
                      padding: 10px;
                      font-weight: bolder;
                  }
                }
                 .img{
                     width: 100%;
                     height: 100%;
                     top:0;
                     position: absolute;
                 }
            }
            .name{
                margin: 16px 10px;
                p{
                    margin-left: 12px;
                }
                .text{
                    color: #bbbbbb;
                    font-size: 12px;
                }
            }
            .price{
                color: red;
                margin: 16px 12px;
                height: 30px;
                font-size: 12px;
                .shop{
                    display: inline-block;
                    position: absolute;
                    .shopcart{
                        width: 90px;
                        background-color: #00b0ff;
                        color: white;
                        display: block;
                        height: 24px;
                        position: absolute;
                        line-height: 24px;
                        text-align: center;
                        left: 240px;
                        margin: -16px -12px;
                        z-index: 5;
                    }
                    .control{
                        left: 240px;
                        margin: -16px -12px;
                        z-index: 4;
                        color: black;
                    }
                }
            }
        }
        .wrapper{
            margin: 16px 5px;
            .title{
                margin-left: 12px;
            }
            .info{
                margin:10px 16px;
                font-size: 12px;
                color:  #4d555d;
            }
        }
        .content{
            margin: 16px 12px;
        }
        .message{
            .food-mess{
                margin: 24px 12px;
                .time{
                    font-size: 12px;
                    color: #7e8c8d;
                }
                .text{
                    display: inline-block;
                    position: absolute;
                    right: 0;
                    .use{
                        font-size: 12px;
                        right: 17px;
                    }
                    img{
                        border-radius: 50px;
                        right: 0;
                    }
                }
                .icon{
                    .icon-dianzan,.icon-chaping{
                        margin-right: 4px;
                        line-height: 16px;
                        color: #7e8c8d;
                        font-size: 12px;
                    }
                    .icon-dianzan{
                        color: rgb(0,160,220);
                    }
                    .icon-chaping{
                        color: gray;
                    }
                }
            }
            .no-rating{
            padding: 30px 12px;
            font-size: 12px;
            color: rgb(147,153,159);
            }
            &.move-enter-active{
                animation: ani1 .7s;
            }
            &.move-leave-active{
                animation: ani1 .7s reverse;
            }
            @keyframes ani1 {
                from{ transform:translate(100%,0) }
                to{transform:translate(0,0)}
            }
        }
    }
</style>