<template>
<div class="seller">
    <div class="content-top">
        <div class="title">
            <div class="star-wrapper">
                <p>{{seller.name}}</p>
                <star :size="36" :score="seller.score"></star>
                <span>({{seller.ratingCount}})</span>
                <span>月售{{seller.sellCount}}单</span>
            </div>
            <div class="love" >
                <div class="changeglay">
                    <img  v-show="shows" @click="cli" src="./2.png"/>
                    <p v-show="showe">收藏</p>
                </div>
                <div class="changered">
                    <img  @click="cli" v-show="showred" src="./1.png"/>
                    <p v-show="showes">已收藏</p>
                </div>

            </div>
            <div class="total">
                <div class="totals">
                    <p class="price">起送价</p>
                    <p class="num">{{seller.minPrice}}<span>元</span></p>
                </div>
                <div class="totals">
                    <p class="price">商家配送</p>
                    <p class="num">{{seller.deliveryPrice}}<span>元</span></p>
                </div>
                <div class="totals">
                    <p class="price">平均配送时间</p>
                    <p class="num">{{seller.deliveryTime}}<span>分钟</span></p>
                </div>
            </div>
        </div>
    </div>
    <div class="content">
        <div class=" notice">
         <p>商家公告</p>
         <p class="res">{{seller.bulletin}}</p>
        </div>
        <div class=" preferential">
            <div v-for="sell in seller.supports" :key="sell">
                <div >{{sell.description}}</div>
            </div>
        </div>
    </div>
    <div class="content-img">
       <p>商家实景</p>
        <div v-for="sell in seller.pics" :key="sell" class="pics">
            <img width="100" height="100"  :src="sell"/>
        </div>
    </div>
    <div class="message">
        <p>商家信息</p>
        <div class="infos" v-for="item in seller.infos" :key="item">
            <ul>
                <li>{{item}}</li>
            </ul>
        </div>
    </div>
</div>
</template>
<script>
    import star from '../star/star'
    export default {
        name: 'seller',
        props:['seller'],
        components:{
            'star':star
        },
        data(){
            return{
                showred:false,
                showes:false,
                showe:true,
                shows:true
            }
        },
        methods:{
            cli(){
                this.showred=!this.showred;
                this.showes=!this.showes;
                this.showe=!this.showe;
                this.shows=!this.shows;
                /*let storage = window.localStorage;
                storage.showred = this.showred;*/
            }
        }
    }
</script>

<style lang="scss">
.seller{
    top: 162px;
    bottom: 48px;
    position: absolute;
    width: 100%;
    left: 0;
    overflow: hidden;
    overflow-y: scroll;
    .content-top{
        height: 110px;
        border-bottom: 1px solid rgba(7,17,27,0.1);
        border-top: 1px solid rgba(7,17,27,0.1);
        margin-top: 10px;
        .title{
            .star-wrapper{
                width: 80%;
                display: inline-block;
                p{
                    margin: 5px auto;
                    margin-left: 12px;
                }
                span{
                    font-size: 12px;
                    margin: 5px auto;
                    margin-left: 12px;
                    color: #7e8c8d;
                }
            }
            .love{
               display: inline-block;
                 width: 20%;
                height: 48px;
                .changered{
                    right:0px;
                    position: absolute;
                }
                .changeglay{
                    right: 12px;
                    position: absolute;
                }
            }
            .total{
                display: flex;
                .totals{
                    height: 20px;
                    text-align: center;
                    line-height: 20px;
                    width: 30%;
                    .price{
                        color: #7e8c8d;
                        font-size: 12px;
                    }
                    .num{
                        font-size: 18px;
                        span{
                            font-size: 14px;
                        }
                    }
                }

            }
        }
    }
    .content{
        border-bottom: 1px solid rgba(7,17,27,0.1);
        border-top: 1px solid rgba(7,17,27,0.1);
        margin-top: 10px;
        .notice{
            p{
               margin-left: 10px;
            }
            .res{
                margin: 5px 18px;
                color: red;
                font-size: 12px;
                padding-bottom: 10px;
                border-bottom: 1px solid rgba(7,17,27,0.1);
            }
        }
        .preferential{
            border-bottom: 1px solid rgba(7,17,27,0.1);
            div{
                div{
                    font-size: 12px;
                    margin: 10px 16px;
                }
            }
        }
    }
    .content-img{
        border-bottom: 1px solid rgba(7,17,27,0.1);
        border-top: 1px solid rgba(7,17,27,0.1);
        margin-top: 10px;
        padding: 10px 12px;
        overflow: hidden;
        white-space: nowrap;
        overflow-x: scroll;
        p{
            margin-left: 12px;
            margin-bottom: 10px;
        }
        .pics{
            display: inline-block;
            padding: 0 5px ;
        }
    }
    .message{
        border-bottom: 1px solid rgba(7,17,27,0.1);
        border-top: 1px solid rgba(7,17,27,0.1);
        margin-top: 10px;
        p{
            margin-left: 12px;
        }
        .infos{
            margin: 5px 20px;
            border-top: 1px solid rgba(7,17,27,0.1);
            font-size: 12px;
            ul li{
                list-style: none;
                margin-top: 15px;
                margin-bottom: 10px;
            }
        }
    }
}
</style>