<template>

<div class="ratings">
    <div class="evaluate">
        <div class="left-evaluate">
             <h2 class="score">{{seller.score}}</h2>
             <div class="title">综合评分</div>
            <div class="rank">高于周边商家{{seller.rankRate}}%</div>
        </div>
        <div class="right-evaluate">
             <div class="score-wrapper">
                <span class="title">服务态度</span>
                 <span class="star-wrapper">
                     <star :size="36" :score="seller.serviceScore"></star>
                 </span>
                <span class="score">{{seller.serviceScore}}</span>
             </div>
             <div class="score-wrapper">
                <span class="title">商品评分</span>
                 <span class="star-wrapper">
                     <star :size="36" :score="seller.foodScore"></star>
                 </span>
                <span class="score">{{seller.foodScore}}</span>
            </div>
             <div class="delivery-wrapper">
        <span class="title">送达时间</span>
        <span class="delivery">{{seller.deliveryTime}}分钟</span>
    </div>
        </div>
    </div>
    <evaluate :ratings="rating" @child-event="parentEvent"  @child-event2="parentEvent2"></evaluate>
    <div class="ratings-wraper">
        <ul>
            <li class="ratings-text" v-show="needShow(item.rateType,item.text)" v-for="(item,index) in rating" :key="index">
              <div class="img-avatar">
                  <img width="28" height="28" :src="item.avatar"/>
              </div>
              <div class="content">
                  <div class="name">
                      <span class="user">{{item.username}}</span>
                      <span class="date">{{format(item.rateTime)}}</span>
                  </div>
                  <div class="star-wrapper">
                      <star :size="24" :score="item.score"></star>
                      <span class="deliveryTime" v-show="item.deliveryTime">{{item.deliveryTime}}分钟送达</span>
                  </div>
                  <p class="text">{{item.text}}</p>
                  <div class="recommend" v-show="item.recommend">
                      <div  v-if="item.recommend.length">
                          <img src="./timg1.jpg"/>
                          <span class="recom" v-for="(items,index) in item.recommend" :key="index">{{item.recommend[index]}}</span>
                      </div>
                  </div>
              </div>
            </li>
        </ul>
    </div>
</div>
</template>
<script>
    import star from "../star/star"
    import evaluate from  '../evaluate/evaluate'
    import {formatDate} from "../js/formatDate"
    export default {
        name: 'ratings',
        components: {
            'star': star,
            evaluate
        },
        props: ['rating', 'seller'],
        data() {
            return {
                   active:false,
                   type:2
            }
        },
        methods:{
            format(time) {
                let date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            },
            parentEvent(type){
             this.type=type;
            },
            parentEvent2(active){
                this.active=active;

            },
            needShow(t,text){
                if(this.active){
                    if (this.type==2 && text!=''){
                        console.log(this.type);
                        return true;
                    }else if (this.type==t&&text!=''){
                        console.log(this.type);
                        return true;
                    }
                }else {
                    console.log(this.type);
                    console.log(t);
                    if (this.type==2){
                        console.log(this.type);
                        return true
                    }else if (this.type==t){
                        console.log(t);
                        return true;
                    }
                }
            }
        }
    }
</script>

<style lang="scss">
        .ratings {
            top: 174px;
            bottom: 48px;
            position: absolute;
            width: 100%;
            left: 0;
            overflow: hidden;
            overflow-y: scroll;
            .evaluate {
                padding: 10px 0;
                 border: 1px solid gainsboro;
                display: flex;
                padding: 0 18px;
                .left-evaluate {
                    padding: 6px 0;
                    flex: 0 0 137px;
                    width: 137px;
                    border-right: 1px solid rgba(7, 17, 27, 0.1);
                    text-align: center;
                    .score {
                        margin-bottom: 6px;
                        line-height: 28px;
                        font-size: 24px;
                        color: rgb(255, 153, 0);
                    }
                    .title {
                        margin-bottom: 8px;
                        line-height: 12px;
                        font-size: 12px;
                        color: rgb(147, 153, 159);
                    }
                    .rank {
                        line-height: 10px;
                        font-size: 10px;
                        color: rgb(147, 153, 159);
                    }
                }
                .right-evaluate {
                    flex: 1;
                    padding: 6px 0 6px 24px;
                    .score-wrapper {
                        margin-bottom: 8px;
                        font-size: 0;
                        .star-wrapper{
                            margin-top: 18px;
                            padding: 2px 0;
                            text-align: center;
                        }
                        .title {
                            display: inline-block;
                            line-height: 18px;
                            vertical-align: top;
                            font-size: 12px;
                            color: rgb(7, 17, 27);
                        }
                        .score {
                            display: inline-block;
                            line-height: 18px;;
                            vertical-align: top;
                            font-size: 12px;
                            color: rgb(255, 153, 0);
                        }
                    }
                    .delivery-wrapper {
                        font-size: 0;
                        .title {
                        line-height: 18px;
                        font-size: 12px;
                        color: rgb(7, 17, 27);
                        }
                        .delivery {
                        margin: 12px;
                        font-size: 12px;
                        color: rgb(147, 153, 159);
                        }
                      }
                }
            }
            .ratings-wraper{
                .ratings-text{
                    display: flex;
                    .img-avatar{
                        flex: 0 0 28px;
                        width: 28px;
                        margin:0 12px;
                        img{
                            border-radius: 50px;
                        }
                    }
                    .content{
                       margin-right: 12px;
                        font-size: 12px;
                        color: #93999f;
                        .name{
                            color: black;
                            .date{
                                right: 12px;
                                color: #93999f;
                                position: absolute;
                            }
                        }
                        .star-wrapper{
                            padding: 2px 0;
                            text-align: left;
                        }
                        .text{
                             color: black;
                        }
                        .recommend{
                            height: 50px;
                            .img{
                                width: 20px;

                            }
                            .recom{
                                margin-right: 5px;
                                border: 1px solid #93999f;
                                padding: 0 5px;
                            }
                        }
                    }
                }
            }
        }
</style>