<template>
    <link rel="stylesheet" href="//at.alicdn.com/t/font_2003341_yt9hn6sibwa.css">
<div id="goods">
    <div class="asd">
        <aside class=" left_side">
            <div class="menu">
                <ul>
                    <li v-for="(item,index) in goods" :key="index" @click="clickmenu(index)"
                        :class="{active:num==index}" class="menu-item">
                        <span class="text">{{item.name}}</span></li>
                </ul>
            </div>
        </aside>
        <section class="right_side" ref="foods">
            <ul>
                <li class="content" v-for="item in goods" :key="item">
                    <h5 class="title">{{item.name}}</h5>
                    <ul>
                        <li v-for="(food,index) in item.foods" @click="selected(food)" :key="index" class="food-img">
                            <div class="food-icon">
                                <img :src="food.icon" style="width: 57px;height: 57px"/>
                            </div>
                            <div class="food-content">
                                <h4 class="name">{{food.name}}</h4>
                                <p class="description">{{food.description}}</p>
                                <div class="achievement" >
                                    <span class="count">月售{{food.sellCount}}份</span>
                                    <span>好评率{{food.rating}}%</span>
                                </div>
                                <div class="price">
                                    <span class="now">¥{{food.price}}</span>
                                    <span v-if="food.oldPrice" class="old">¥{{food.oldPrice}}</span>
                                     <control :food="food"></control>
                                </div>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </section>
    </div>
    <food :food="seletedFood" ref="food"></food>
</div>
</template>
<script>
    import food from '../food/food'
    import control from "../control/control"
    export default {
        name:'goods',
        components:{
          'food':food,
            control,
        },
        data(){
            return {
                seletedFood:[],
                num:0,
                area:0,
                areaArr:[0],
                currentHeight:0,
                goodsListHeight:[],
                h:0
            }
        },
        methods:{
            clickmenu(index){
                this.currentHeight=0;
                for(let i=0;i<index;i++){
                    this.currentHeight+=this.goodsListHeight[i];
                }
                this.$refs.foods.scrollTop=this.currentHeight;
                this.num=index;
            },
            allHeight(){
                let list=this.$refs.foods.getElementsByClassName('content');
                for(let i=0;i<list.length;i++){
                   let height=list[i].clientHeight;
                   this.goodsListHeight.push(height);
                   this.area+=list[i].clientHeight;
                   this.areaArr.push(this.area)
                }
            },
            selected(food){
                this.seletedFood=food;
                this.$refs.food.show();
            },
        },
        mounted(){
            this.allHeight();
            let _this=this;
            _this.$refs.foods.onscroll=function () {
                _this.h=this.scrollTop;
                for(let i=1;i<_this.areaArr.length;i++){
                    if(_this.h>_this.areaArr[i-1]&&_this.h<_this.areaArr[i]){
                        _this.num=i;
                    }
                }
            }
        },
        props:["goods"] ,

    }
</script>

<style lang="scss">
  #goods{
      width: 100%;
      bottom: 48px;
      overflow: hidden;
      position: absolute;
      top: 152px;
      .asd{
          display: flex;
          height: 100%;
          .left_side{
              width: 80px;
              position: relative;
              line-height: 30px;
              margin-left: 10px;
              flex: 0 0 80px;
              background: #f3f5f7;
              vertical-align: baseline;
              .menu{
                  ul li{
                      list-style: none;
                  }
                  .menu-item{
                      display: table;
                      height: 54px;
                      padding: 0px 12px;
                      box-sizing: border-box;
                      font-size: 12px;
                      color: rgb(20,20,20);
                      .text{
                          display: table-cell;
                          vertical-align: middle;
                          line-height: 14px;
                          width: 56px;
                          position: relative;
                      }
                  }

                  .active{
                      background-color: #ffffcc;
                  }
              }
          }
          .right_side{
              overflow-y: scroll;
              position: relative;
              flex: 1;
              .content{
                  list-style: none;
                  .title {
                      position: relative;
                      color: rgb(147, 153, 159);
                  }
                  .food-img{
                      list-style: none;
                      display: flex;
                      margin: 5px 12px;
                      padding: 5px 12px;
                      border-bottom:1px solid rgba(7,17,27,0.1);
                      .food-icon{
                          flex: 0 0 57px;
                          margin-right: 10px;
                      }
                      .food-content{
                          flex: 1;
                          display: block;
                          .name{
                              height: 14px;
                              line-height: 14px;
                              font-size: 14px;
                              color: rgb(7,17,27);
                          }
                          .description,.achievement{
                              margin-top: 5px;
                              line-height: 12px;
                              font-size: 10px;
                              color: rgb(147,153,159);
                          }
                          .achievement{
                              .count{
                                  margin-right: 12px;
                              }
                          }
                          .price{
                              line-height: 24px;
                              .now{
                                  color: red;
                                  font-size: 14px;
                                  margin-right: 12px;
                              }
                              .old{
                                  font-size: 10px;
                                  text-decoration: line-through;
                                  color: rgb(147,153,159);
                              }
                          }
                      }
                  }
              }
          }
      }
  }
</style>