<template>
  <div class="star" :class="starType">
    <span v-for="(itemClass,index) in itemClasses" :key='index' class="star-item" :class="itemClass" ></span>
  </div>
</template>

<script>
export default {
  props: ['size','score'],
  data() {
    return {
    };
  },
  computed: {
    starType() {
      return 'star_' + this.size
    },
    itemClasses() {
      let itemClasses = [];
      let score = Math.floor(this.score*2)/2;
      for (let i = 1; i <= score; i++) {
        itemClasses.push('on');
      }
      if (score % 1 != 0) {
        itemClasses.push('half');
      }
      while (itemClasses.length < 5) {
        itemClasses.push('off');
      }
      return itemClasses;
    }
  }
}
</script>

