<template>
    <link rel="stylesheet" href="//at.alicdn.com/t/font_2003341_mhgmdb1q0zq.css">
<div class="shopping">
    <div class="content" @click="foodshow">
      <div class="content-left">
          <div class="icon">
              <i class="iconfont icon-ai66"></i>
              <div class="num" v-show="totalnum>0">{{totalnum}}</div>
          </div>
          <div class="price" :class="{pricenum:totalprice>0}">
           <span>¥{{totalprice}}元</span>
          </div>
          <div class="text">
              <span>另需配送费4¥</span>
          </div>
      </div>
      <div class="content-right" :class="{active:payclass}">
          <div class="pay">{{paydelivery}}</div>
      </div>
    </div>
    <transition name="blod">
        <div class="listshop" v-show="listshops">
            <div class="list-head">
                <span class="cart">购物车</span>
                <span class="clear" @click="clear">清空</span>
            </div>
            <div class="list-content">
               <ul>
                   <li v-for="(item,index) in cart" :key="index" class="cartfood">
                       <div class="name">{{item.name}}</div>
                       <div class="foodprice">¥{{item.price*item.count}}</div>
                       <div class="controlfood">
                           <control :food="item"></control>
                       </div>
                   </li>
               </ul>
            </div>
        </div>
    </transition>
</div>
</template>
<script>
    import control from "../control/control"
    export default {
        components: {control},
        name: 'shopping',
        data(){
            return{
                  listshops:false
            }
        },
        props:['food','goods','seller'],
        methods:{
            foodshow(){
                if (!this.totalnum) {
                    return ;
                }
                this.listshops=!this.listshops;
            },
            clear(){
                this.cart.forEach((food)=>{
                    food.count=0;
                })
                  }
        },
        computed:{
            totalprice(){
                let price=0;
                this.goods.forEach((item)=> {
                item.foods.forEach(food=> {
                   price+=food.count*food.price;
                });
            });
                return price;
            },
            totalnum(){
                let num=0;
                this.goods.forEach((item)=> {
                    item.foods.forEach(food=> {
                        num+=food.count;
                    });
                });
                return num;
            },
            paydelivery(){
                if(this.totalprice===0){
                    return '¥'+this.seller.minPrice+'元起送'
                }else if(this.totalprice<this.seller.minPrice){
                    let  disparity=this.seller.minPrice-this.totalprice;
                    return '还差'+disparity+'起送'
                }else {
                    return '去结算'
                }
            },
            payclass(){
                if(this.totalprice>=this.seller.minPrice){
                    return true;
                }else {
                    return false;
                }
            },
            cart(){
                let cartnum=[];
                this.goods.forEach((item)=> {
                    item.foods.forEach(food=> {
                       if(food.count>0){
                           cartnum.push(food);
                       }
                    });
                });
                return cartnum;
            }
            },
        watch:{
            cart(){
                if (this.cart.length<1){
                    this.listshops=false;
                }
            }
        }
    }
</script>

<style lang="scss">
 .shopping{
     z-index: 50;
     width: 100%;
     left: 0;
     bottom: 0;
     position: fixed;
     height: 48px;
     .content{
         display: flex;
         width: 100%;
         height: 48px;
         z-index: 5;
         .content-left{
             flex: 1;
             width: 65%;
             position: absolute;
             height: 48px;
             display: inline-block;
             background-color:#141d27 ;
             color: white;
             .icon{
                 position: relative;
                 width: 50px;
                 height: 50px;
                 margin-left: 12px;
                 display: inline-block;
                 border-radius: 50px;
                background-color: #7e8c8d;
                 i{
                     font-size: 45px;
                 }
                 .num{
                     position: absolute;
                     top:0;
                     right: 0;
                     width: 24px;
                     height: 16px;
                     line-height: 16px;
                     background-color: red;
                     color: white;
                     font-size: 8px;
                     font-weight: bold;
                     border-radius: 16px;
                     text-align: center;
                 }
             }
             .price{
                 padding-left: 16px;
                 margin-left: 9px;
                 display: inline-block;
                 line-height: 48px;
                 color: rgba(255,255,255,0.4);
                 &.pricenum{
                     color: white;
                 }
             }
             .text{
                 padding-left: 12px;
                 margin: 16px;
                 display: inline-block;
                 font-size: 12px;
                 color: rgba(255,255,255,0.4)
             }
         }
         .content-right{
             height: 48px;
             width: 35%;
             flex: 0 0 35%;
             position: absolute;
             right: 0;
             background-color: #2b333b;
             .pay{
                 line-height: 48px;
                 text-align: center;
                 color: rgba(255,255,255,0.4);
             }
             &.active{
                 background-color: green;
                 color: white;
             }
         }
     }
     .listshop{
         width: 100%;
         position: absolute;
         bottom: 48px;
         left: 0;
         z-index:-1;
         &.blod-enter-active{
             animation: ani4 .5s;
         }
         &.blod-leave-active{
             animation: ani4 .5s reverse;
         }
         @keyframes ani4 {
             from{opacity: 0}
             to{opacity: 1}
         }
         .list-head{
             height: 40px;
             background: #fff;
             line-height: 40px;
             border-bottom: 1px solid rgba(7,17,27,0.1);
             .cart{
                 padding: 0 18px;
             }
             .clear{
                 padding: 0 18px;
                 color:#00b0ff;
                 float: right;
             }
         }
         .list-content{
             padding: 0 18px;
             max-height: 217px;
             overflow: hidden;
             background: #fff;
             .cartfood{
                 position: relative;
                 padding: 0 12px;
                 list-style: none;
                 box-sizing: border-box;
                 border-1px:rgba(7,17,27,0.1);
                 .name{
                     line-height: 24px;
                     font-size: 12px;
                     color: black;
                     display: inline-block;
                 }
                 .foodprice{
                     position: absolute;
                     color: orangered;
                     display: inline-block;
                     right: 120px;
                     line-height: 24px;
                     font-size: 14px;

                 }
                 .controlfood{
                     right: 0;
                     display: inline-block;
                     position: absolute;
                 }
             }
         }
     }
 }
</style>